* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #fff6d8;
  font-family: "Inter", sans-serif;
  font-size: 1rem;
}

/* ================= HEADER ================= */
.site-header {
  padding: 1.25rem 0;
}
.site-header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.site-header .logo {
  font-size: 1.375rem;
  font-weight: 700;
}
.site-header .nav a {
  margin: 0 0.9375rem;
  font-size: 0.9375rem;
  font-weight: 500;
  text-decoration: none;
  color: #222;
}
.site-header .nav a:hover {
  color: #000;
}
.site-header .auth {
  display: flex;
  align-items: center;
}
.site-header .auth button {
  font-size: 0.875rem;
  border-radius: 1.25rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
}
.site-header .auth .btn-outline {
  background: transparent;
  border: 0.0625rem solid #ffcc00;
}
.site-header .auth .btn-primary {
  background: #ffcc00;
  border: none;
  margin-left: 0.625rem;
}

/* ================= HERO ================= */
.hero {
  padding: 3.75rem;
}
.hero .hero-wrapper {
  display: flex;
  gap: 3.75rem;
}

/* LEFT */
.service-tabs {
  width: 32.5rem;
}
.service-tabs .service-tab {
  background: #fff;
  border-radius: 1rem;
  padding: 1.25rem;
  margin-bottom: 1rem;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.4s ease;
}
.service-tabs .service-tab .tab-head {
  display: flex;
  gap: 0.625rem;
  font-size: 1.125rem;
  font-weight: 600;
}
.service-tabs .service-tab .tab-body {
  max-height: 0;
  opacity: 0;
  transform: translateY(0.625rem);
  transition: all 0.4s ease;
  pointer-events: none;
}
.service-tabs .service-tab .tab-body h2 {
  font-size: 1.75rem;
  margin: 1.25rem 0 0.625rem;
}
.service-tabs .service-tab .tab-body p {
  font-size: 0.9375rem;
  margin-bottom: 1.25rem;
  color: #555;
}
.service-tabs .service-tab .tab-body .actions {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}
.service-tabs .service-tab .tab-body .actions .call {
  border: 0.125rem solid #ffcc00;
  padding: 0.625rem 1rem;
  border-radius: 1.875rem;
  text-decoration: none;
  color: #000;
}
.service-tabs .service-tab .tab-body .actions .primary {
  background: #ffcc00;
  border: none;
  padding: 0.625rem 1.25rem;
  border-radius: 1.875rem;
}
.service-tabs .service-tab .tab-body .actions .outline {
  background: transparent;
  border: 0.125rem solid #ffcc00;
  padding: 0.625rem 1.25rem;
  border-radius: 1.875rem;
}
.service-tabs .service-tab .tab-body .actions .link {
  text-decoration: none;
  font-size: 0.875rem;
  color: #000;
}
.service-tabs .service-tab.active {
  box-shadow: 0 1.25rem 2.5rem rgba(0, 0, 0, 0.12);
}
.service-tabs .service-tab.active .tab-body {
  max-height: 37.5rem;
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
}

/* RIGHT */
.service-images {
  position: relative;
  min-height: 26.25rem;
}
.service-images .service-image {
  position: absolute;
  inset: 0;
  opacity: 0;
  transform: translateY(1.875rem);
  transition: all 0.5s ease;
}
.service-images .service-image img {
  width: 100%;
  max-width: 23.75rem;
}
.service-images .service-image.active {
  position: relative;
  opacity: 1;
  transform: translateY(0);
}

